body {
  /*background:#17181a;*/
  /*background:#ededed;*/
  margin: 10px;
  padding: 0;  

  font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif; 
  font-weight: 400;
  font-size: 12px;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}


.TokenPoolDetails {
  margin-top:10px;
  margin-bottom:10px;
}
.TokenPoolHistory {
  
}
.TokenChart {
  width:calc(100% - 5px);
  display: inline-block;
  height:200px;
  border:1px solid #cccccc;
}

div.rt-tr {
  align-items: center;
}

.right {
  text-align: right;
}

/* attribution text for loading gif */
.Attribution {
  position: fixed;
  top:calc(100% - 50px);
  height:50px;
  width:calc(100% - 20px);
}

/* truncated text */
.truncate {
  width: 100px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;  
}
 
 /* Loading gif */
 .LoadingImage {
  position: fixed;
  top:200px;
  left:calc(50% - 120px);
 }

 .Logo {
  position: fixed;
  font-size: 30px;
  top:-20px;
  left:15px;
 }

 .Dropdown-root {
  position: fixed;
  width:calc(100% - 80px);
  z-index: 999;
  left:60px;
}

.TokenDetails {
  position: fixed;
  top:50px;
  
  width:calc(100% - 30px);
}

.TokenHistory {
  overflow-y: scroll;

  position: absolute;
  top:360px; 
  bottom:50px;
  width:calc(100% - 25px);
}
